.profilHost_contenair{
    display: flex;
    align-items: center;
    background-color: var(--main-bg-color);
}

.profilHost_name{
    font-weight: 500;
    font-size: 18px;
    line-height: 142.6%;
    text-align: end;
    white-space: pre-wrap;
    margin-right: 10px;
}

.profilHost_face{
    height: 64px;
    width:64px; 
    object-fit: cover;
    border-radius: 50%;
}

.collapse_contenair{
    display: flex;
    justify-content: space-between;
}

  /* -------------------  Responsive ----------------------*/
  
  
  @media screen and (max-width: 576px) {

    .profilHost_name{
        font-size: 12px;
    }
    
    .profilHost_face{
        height: 32px;
        width:32px; 
    }
    
  }